/* ROOT */
:root {
	--custom-guild-shop-preview-pill-shadow-dark: -4px 5px #1d1d1d;
	--custom-guild-shop-preview-pill-shadow-light: -4px 5px #d7dce8;
	--brand: #5865F2;
	--brand-gradient: linear-gradient(90deg,#8ea1e1,#7289da);
	--greyple: #99aab5;
	--sub-gray: #a2a3a7;
	--dark-not-black: #2c2f33;
	--status-green: #43b581;
	--text-link: #00b0f4;
	--off-white: #f6f6f6;
	--white: #fff;
	--not-quite-black: #23272a;
	--not-quite-black-rgb: 35,39,42;
	--black: #000;
	--border-black: #202124;
	--ekko-red: #de2761;
	--error: #fa777c;
	--page-max-width: 1260px;
	--page-max-width-med: 1024px;
	--top-bar-height: 48px;
	--menu-background: #313338;
	--header-white: #F2F3F5;
	--messages-black: #313338;
	--messages-white: #DBDEE1;
	--messages-gray: #BCBAC5;
	--member-bg: #2B2D31;
	--member-bg-hover: #4E505899;
	--member-color: #949BA4;
}

body {
	max-width: 100%;
	overflow-x: hidden;
	font-family: 'gg sans';
	color: var(--off-white);
	background: var(--white);
	text-rendering: optimizeLegibility;
}

.bold {
	font-weight: 600;
}

.italic {
	font-style: italic;
}

.error {
	color: red;
}

sub {
  font-size: 9pt;
  color: var(--sub-gray);
}

hr {
	height: 1px;
	border: none;
	background: #2c2f33;
}

/* INPUT */
input[type=text], input[type=email], input[type=textarea], input[type=password] {
	outline: none;
}

/* LINKS */
a {
	color: var(--text-link);
	text-decoration: none;
	font-weight: 500;
}

/* BUTTONS */
button {
	border: none;
}

button:hover {
	background: #404248;
  color: white;
}

/* ICONS */
.small-icon {
	height: 20px;
	width: 20px;
}

.server-icon {
	height: 48px;
	min-width: 48px;
	align-items: center;
	justify-content: center;
	border-radius: 50%;
}

.large-icon {
	height: 82px;
	width: 82px;
	border-radius: 50%;
	border: solid var(--not-quite-black);
	border-width: 1px;
	align-items: center;
	justify-items: center;
}

.message-avatar {
	height: 40px;
	border-radius: 50%;
}

.profile-avatar {
	height: 32px;
	border-radius: 50%;
}

/* ANIMATION */
@keyframes fadein {
  0% {
      transform: scale(0.5);
      opacity: 50%;
  }

  100% {
      transform: scale(1);
      opacity: 100%;
  }
}