.profile-button {
  /* margin-top: 3px; */
  margin-bottom: 5px;
  font-size: 12px;
  font-weight: 600;
  background: #2B2D31;
  color: white;
  height: 39px;
  border-radius: 5px;
  text-align: center;
  justify-content: center;
  vertical-align: center;
}

.profile-button img {
  margin-bottom: -5px;
  margin-right: 8px;
}

.profile-dropdown {
  position: fixed;
  bottom: 65px;
  left: 50px;
  background-color: black;
  width: 225px;
  border-radius: 8px;
  color: #fff;
  text-align: left;
  padding: 5px;
  font-size: 15px;
}

.profile-dropdown li {
  margin: 12px 30px;
  font-size: 14px;
}

.profile-dropdown h1 {
  font-size: 20px;
}

.profile-dropdown h2 {
  font-family: 'ABC Ginto Normal';
  font-size: 11px;
  font-weight: 700;
  color: #fff
}

.profile-dropdown button {
  background: none;
  color:#f6f6f6;
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: left;
}